<template>
  <div class="orderinfo">
    <!-- <header class="head clearfix">
        <div @click="backbtn()" class="fl">
            <span class="fl headback"></span>
            <span class="fl"></span>
        </div>
        <h3 class="fl"></h3>
    </header> -->
    <div class="userInfoBox">
        <ul class="userInfo">
            <li class="item ">
                <strong ></strong>
                <input @blur="isName"  v-model="nameValue"   placeholder=""  type="text" value="nameValue" >
            </li>
             <li class="item ">
                <strong ></strong>
                <input  type="text"  @blur="isPhoneNumber"  v-model="phoneNumber" placeholder="" value="phoneNumber" >
           </li> 
            <li class="item ">
                <strong ></strong>
                <!-- <input  type="text" value="  " > -->
                <span>
                    {{selectedInfo.areaItem.areaName+selectedInfo.subdistyictItem.areaName}}
                    {{selectedInfo.cellseletionItem.address+selectedInfo.cellseletionItem.name}}

                </span>
            </li>
            <li class="item ">
                <strong ></strong>
                <input  type="text"   placeholder="5203"  value="" >
            </li>
        </ul>
    </div>
    <div class="collectTimeAndPrice">
        <div class="pickUp ">
            <strong></strong>
            <time>{{time}}</time>
            <div  @click="setTime" class="dataBlock">
                <a href="javaScript:;">  
                </a>
            </div>
        </div>
        <div class="estimatePrice clearfix">
            <strong class="fl"></strong>
            <span class="fr">{{futurePrice}}</span>
        </div>        
    </div>
    <div class="information"></div>
    <div class="nextbutton  ">
        <!-- <a  v-if="!isEnter"   style="background-color:#bfc5c8;"   href="javascript:;"></a> -->
        <router-link  @click="completeAnOrder"  to="/home" ></router-link>

    </div>
    <div class="nextbutton  ">
        <a   @click="completeAnOrder"      href="javascript:;"></a>

    </div>

    <div class="information"></div>
    <div class="bottomInformation">
        <div></div>
        <div>400-8288-999</div>
    </div>


    
    <div  v-show="show" class="changeTimeBox">

        <div class="btnBox">
            <h5></h5>
            <span   @click="setTimeInfoAm()"   ></span>
            <span   @click="setTimeInfoPm()" ></span>
        </div>
    </div>
  </div>
</template>
<script>
import '@/assets/createstyle/tool.css'
import '@/assets/createstyle/orderInfo.css'
import api from '@/api/api.js'
import { mapGetters } from 'vuex';
// import DataTime from '@/components/create/common/dataTime.vue'
export default {
    data () {
        return {
            nameValue:'',
            phoneNumber: '',
            datetime5: '2018-01-11 ',
            time :'',
            show:false,
            infoTm:''
            
           
        }
    },
    computed: mapGetters({
        futurePrice         :   'futurePrice',      //
        appointmentTime     :   'appointmentTime',  //
        selectedInfo        :   'selectedInfo',      //  
        imgsAddress         :   'imgsAddress',      //
        addRessId           :   'addRessId',         //
        categoryAttrOppIds  :   'categoryAttrOppIds'  //
    }),
    methods:{
        completeAnOrder(){  

            api.completeOrder({   
                "app_key": "app_id_1",
                "data": {
                    "address": this.selectedInfo.cellseletionItem.address,  //
                    "linkMan": this.nameValue,
                    "orderItemBean": {
                    "categoryAttrId": 0,                     
                    "orderId": 0,
                    "categoryAttrOppId": 0,
                    "categoryAttrOppIds": this.categoryAttrOppIds,
                    "categoryId": 0
                    },
                    "level": "0",
                    "orderPic": {
                    "new": true,
                    "picUrl": this.picUrlAll,
                    "orderId": 0,
                    "origPic": this.originalAll,
                    "smallPic": this.thumbnailAll,
                    "delFlag": "0"
                    },
                    "isEvaluated": "0",
                    "recyclerId": 0,
                    "companyId": 0,     //ID
                    "unit": "",   // 
                    "areaId": this.selectedInfo.cellseletionItem.areaId,      //id
                    "price": this.futurePrice,   //
                    "qty": 9999,        //  
                    "tel": this.phoneNumber,
                    "communityId": this.selectedInfo.cellseletionItem.id,   //ID
                    "categoryId": this.addRessId.id,    //ID   
                    "memberId": 0,
                    "arrivalPeriod": this.infoTm,
                    "arrivalTime": this.time
                }
            }).then((res)=>{
                console.log(res)

            }).catch((err)=>{
                console.log(err)

            })

        },
        picUrlAll(){

            var b  = []

          let bigPic =  this.imgsAddress.forEach((e)=> {

              b.push(e.bigPicture)
          })

          return b;

        },
        originalAll(){
             var b  = []

          let bigPic =  this.imgsAddress.forEach((e)=> {

              b.push(e.original)
          })

          return b;
        },
        thumbnailAll(){

           var b  = []

          let bigPic =  this.imgsAddress.forEach((e)=> {

              b.push(e.thumbnail)
          })

          return b; 
        },
        setTimeInfoAm(){
            this.show = false;
            this.infoTm = "am"
        },
        setTimeInfoPm(){
            this.show = false;
            this.infoTm = "pm"
        },
        backbtn(){
            this.$router.go(-1);
        },
        setTime(){
            var antThis = this;
            ap.datePicker({
                formate: 'yyyy-MM-dd HH:mm:',
                currentDate: '2018-03-25 15:11:11',
                startDate: '2018-03-01 11:11:11',
                endDate: '2018-12-30 11:11:11'
            }, function(res) {
                
                if(res.date){
                    antThis.show = true;
                    antThis.time = res.date;
                }
            
            });

        },
        isName(){
            var reg = RegExp();
            var str = this.nameValue;               
            reg=/^([\u4e00-\u9fa5]){2,7}$/;       //2-7
            if(!reg.test(str)){       
                    alert(",!");//   
            }       
        },
        isPhoneNumber(){
            var reg = RegExp();
            reg=/^[1][3,4,5,7,8][0-9]{9}$/;  
            if (!reg.test(this.phoneNumber)) {  
                    alert("")
                } else {  
                    
                }

            }
       
    }
   
  
}
</script>






